'use strict'

const vidslice = (state = {}, action) => {
  switch (action.type) {
    case 'ADD_VIDEO_URL':
      return { ...state, video: { url: action.url } }
      break;
    case 'ADD_FULL_VIDEO_TO_CLIPLIST':
      return { ...state, clips: [ action.fullVideo ] } 
      break;
    default:
      return state
  }
}

export default vidslice
